/** @jsx React.DOM */

var $ = require('jquery')
var React = require('react')
var selectize = require('selectize')

var tabUrls = {
	'problems': '/api/labs/all/problems',
	'posts': '/api/labs/all',
};

var Header = React.createClass({displayName: 'Header',

	getInitialState: function () {
		return {
			changed: false,
			tab: this.props.startTab || 'posts',
			ordering: 'global',
		};
	},

	componentDidUpdate: function () {
		//
		if (!this.intializedProblems && this.state.tab === 'problems') {
			this.intializedProblems = true;
			var t = $(this.refs.topic.getDOMNode()).selectize({
				plugins: ['remove_button'],
				maxItems: 5,
				multiple: true,
				labelField: 'name',
				valueField: 'id',
				searchField: 'name',
				options: [
					{ name: 'Álgebra', id: 'algebra', },
					{ name: 'Combinatória', id: 'combinatorics', },
					{ name: 'Geometria', id: 'geometry', },
					{ name: 'Teoria dos Números', id: 'number-theory', }
				],
			});
			t[0].selectize.addItem('algebra')
			t[0].selectize.addItem('combinatorics')
			t[0].selectize.addItem('geometry')
			t[0].selectize.addItem('number-theory')
			t[0].selectize.on('change', this.onChangeSelect);
			//
			var l = $(this.refs.level.getDOMNode()).selectize({
					plugins: ['remove_button'],
					maxItems: 5,
					multiple: true,
					labelField: 'name',
					valueField: 'id',
					searchField: 'name',
					options: [
						{ name: 'Nível 1', id: 1, },
						{ name: 'Nível 2', id: 2, },
						{ name: 'Nível 3', id: 3, },
						{ name: 'Nível 4', id: 4, },
						{ name: 'Nível 5', id: 5, },
					],
			});
			l[0].selectize.addItem(1)
			l[0].selectize.addItem(2)
			l[0].selectize.addItem(3)
			l[0].selectize.addItem(4)
			l[0].selectize.addItem(5)
			l[0].selectize.on('change', this.onChangeSelect);
		}
	},

	onChangeSelect: function () {
		this.setState({ changed: true });
	},

	query: function () {
		var topic = this.refs.topic.getDOMNode().selectize.getValue(),
				level = this.refs.level.getDOMNode().selectize.getValue();
		this.props.render(url, { level: level, topic: topic },
			function () {
				this.setState({ changed: false })
			}.bind(this)
		)
	},

	gotoProblems: function () {
		this.props.render(tabUrls['posts'], { ordering: 'global' });
	},
	gotoPosts: function () {
		this.props.render(tabUrls['posts'], { ordering: 'global' });
	},

	render: function () {

		var makeSetter = function (ns) {
			var self = this;
			return function () {
				if (self.source !== ns) {
					var url = tabUrls[ns];
					if (!url)
						throw new Error('WTF');
					self.setState({ tab: ns });
					self.props.render(url,
						function () {
							// self.setState({ changed: false })
						}
					)
				}
			}
		}.bind(this)

					// <div className='label'>
					// 	Mostrando posts
					// </div>
		if (this.state.tab === 'posts') {
			var SearchBox = (
				React.createElement("div", null
				)
			);
					// <div className='label'>
					// 	Mostrando problemas
					// </div>
		} else if (this.state.tab === 'problems') {
			var SearchBox = (
				React.createElement("div", {className: "stream-search-box"}, 

					React.createElement("select", {ref: "topic", className: "select-topic"}
					), 

					React.createElement("select", {ref: "level", className: "select-level"}
					), 

					React.createElement("button", {className: "new-problem", 
						'data-trigger': "component", 'data-component': "createProblem"}, 
						"Novo Problema"
					), 

					React.createElement("button", {disabled: !this.state.changed, className: "query", onClick: this.query}, 
						"Procurar"
					)
				)
			);
		} else {
			throw new Error('WTF state?', this.state.tab);
		}

		return (
				React.createElement("div", null, 
					React.createElement("nav", {className: "header-nav"}, 
						React.createElement("ul", {className: "tabs"}, 
							React.createElement("li", null, 
								React.createElement("button", {onClick: makeSetter('posts'), 
								className: this.state.tab==='posts' && 'active'}, "Publicações")
							), 
							React.createElement("li", null, 
								React.createElement("button", {onClick: makeSetter('problems'), 
								className: this.state.tab==='problems' && 'active'}, "Problemas")
							)
						), 
						React.createElement("ul", {className: "right"}, 
							React.createElement("li", null, 
								React.createElement("button", {
								className: 'ordering global'+(this.state.ordering === 'global' && 'active')}, 
									React.createElement("i", {className: "icon-publ"})
								)
							), 
							React.createElement("li", null, 
								React.createElement("button", {
								className: 'ordering following'+(this.state.ordering === 'following' && 'active')}, 
									React.createElement("i", {className: "icon-users"})
								)
							), 
							React.createElement("li", null, 
								React.createElement("button", {
								className: 'ordering hot'+(this.state.ordering === 'hot' && 'active')}, 
									React.createElement("i", {className: "icon-whatshot"})
								)
							)
						)
					), 
					SearchBox
				)
			);
	},
})

module.exports = function (app, startTab) {
	function renderWall () {
		app.renderWall.apply(app, arguments);
	}

	React.render(React.createElement(Header, {render: renderWall, startTab: startTab}),
		document.getElementById('qi-header'));
};